.form-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  grid-auto-rows: minmax(200px, auto);
  background-color: white;
}

.form-box1 img {
  width: 90%;
}

.form-box2 {
  padding-left: 5%;
  width: 80%;
}

.box2-h1 {
  font-size: 35px;
  font-weight: bold;
  color: var(--mainGrey);
}

.box2-p {
  font-size: 18px;
  color: var(--mainGrey);
}

.box2-fields {
  padding-top: 5%;
  padding-bottom: 5%;
}

.partner-field {
  padding-bottom: 3%;
}

.partner-field input {
  padding-left: 2%;
  height: 8vh;
  width: 90%;
  font-size: 18px;
}

.checkbox-container {
  float: left;
  display: flex;
  align-items: center;
}

.checkbox-container input {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.checkbox-container a {
  color: var(--mainGrey);
  font-size: 12px;
  font-weight: 300;
}

