.box2-btn {
  width: 20%;
  padding: 10px;
  background-color: var(--mainGreen);
  color: white;
  font-weight: bold;
  border: none;
  font-size: 18px;
  float: right;
  cursor: pointer;
  border-radius: 5px;
}

.box2-btn:hover {
  background-color: #6c836c;
  color: white;
}

@media only screen and (max-width: 780px) {
  .form-wrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 0vh 95vh;
  }
  .form-box1 img {
    width: 100%;
  }
  .form-box2 {
    background-color: rgba(255, 255, 255, 0.6);
    width: 100%;
    padding: 10%;
    padding-top: 15%;
    height: 100%;
    font-size: 50%;
  }

  .box2-fields {
    padding-top: 20%;
  }
  .box2-btn {
    position: absolute;
    z-index: 10;
    float: right;
  }
  .partner-field input {
    background-color: rgba(255, 255, 255, 0.8);
  }
  .checkbox-container {
    padding-left: 40px;
  }

  .checkmark {
    background-color: white;
  }
}

@media only screen and (max-width: 630px) {
  .form-wrapper {
    grid-template-columns: 0fr 1fr;
    grid-template-rows: 0vh 80vh;
  }
  .form-box1 img {
    width: 0px;
    height: 0px;
  }
  .form-box2 {
    padding-top: 10%;
  }

  .box2-fields {
    padding-top: 10%;
  }
}

.inspo-images {
  border: 3px solid #fff;
  padding-top: 0%;
  height: 500px;
  padding-left: 10%;
}

.inspo-image1 {
  width: 45%;
  float: left;
  padding: 2%;
  height: 100%;
}
.inspo-h {
  position: relative;
  left: 45%;
  font-size: 20px;
}
